.wrapper {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 2rem
}

.date {
  outline: 0;
  width: 100%;
  font-size: 2rem;
  border: none;
  padding: 1rem;
  background-color: hsl(0deg, 0%, 95%);
  border-radius: 6px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.button {
  background-color: rgb(142, 142, 255);
  padding: 1rem;
  font-size: 1.3rem;
  border-radius: 6px;
  border: none;
  width: 100%;
  cursor: pointer;
}

.error {
  color: red;
}