.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column nowrap;
}

.icon {
  color: red;
  font-size: 20rem;
  animation: pulse 1s infinite,
              pulsecolor 1s infinite;
// transition: scale 300ms ease-in-out;
}



@keyframes pulse {
	10% {transform: scale(1.1)}
}

@keyframes pulsecolor {
  10% {color: #dd0000}
}